import Mock from "mockjs"
import homeApi from './mockServerData/home'
import user from './mockServerData/user'
import permission from "./mockServerData/permission"
import img from "./mockServerData/img"

// 定义mock请求地址
Mock.mock('/api/home/getData', 'get', homeApi.getStatisticalData)

// 用户列表的数据
Mock.mock('/api/user/add', 'post', user.createUser)
Mock.mock('/api/user/edit','post', user.updateUser)
Mock.mock('/api/user/del','post', user.deleteUser)
Mock.mock(/api\/user\/getUser/,  user.getUserList)


Mock.mock(/api\/permission\/getMenu/, 'post', permission.getMenu)

// 图片列表的数据
Mock.mock('/api/img/add', 'post', img.createPro)
Mock.mock('/api/img/edit','post', img.updatePro)
Mock.mock('/api/img/del','post', img.deletePro)
Mock.mock(/api\/img\/getPro/,  img.getProjList)