import Mock from 'mockjs'

// get请求从config.url获取参数，post从config.body中获取参数
function param2Obj (url) {
  const search = url.split('?')[1]
  if (!search) {
    return {}
  }
  return JSON.parse(
    '{"' +
    decodeURIComponent(search)
      .replace(/"/g, '\\"')
      .replace(/&/g, '","')
      .replace(/=/g, '":"') +
    '"}'
  )
}

let List = []
const count = 20
// id, name, methodNum, imgNum, createdTime, updateTime, methodScore, methodImg, methodName
for (let i = 0; i < count; i++) {
    const lis = Mock.mock({
                id: Mock.Random.guid(),
                name: 'project' + i,
                "methodNum|2-5": 1,
                "imgNum|1-100": 1,
                createdTime: Mock.Random.datetime(),
                updateTime: Mock.Random.datetime(),
            })
    let methodScore = []
    let methodImg = []
    let methodName = []
    const colorArr = ['#FF6600', '#4A7BF7', '#50B347', '#894FC4', '#cccccc']
    for (let m = 0; m < lis.methodNum; m++){
        let imgScore = []
        let imgs = []
        for (let n = 0; n < lis.imgNum; n++){
            imgScore.push(Mock.mock({
                "score|0-5.1": 1
              })),
            imgs.push(Mock.Random.image('200x200', colorArr[m]))
        }
        methodScore.push(imgScore)
        methodImg.push(imgs)
        methodName.push('method' + (m+1))
    }
    lis.methodScore = methodScore
    lis.methodImg = methodImg
    lis.methodName = methodName

    List.push(lis)

}

export default {
  /**
   * 获取列表
   * 要带参数 name, page, limt; name可以不填, page,limit有默认值。
   * @param name, page, limit
   * @return {{code: number, count: number, data: *[]}}
   */
  getProjList: config => {
    const { name, page = 1, limit = 20 } = param2Obj(config.url)
    console.log('name:' + name, 'page:' + page, '分页大小limit:' + limit)
    const mockList = List.filter(user => {
      if (name && user.name.indexOf(name) === -1 && user.addr.indexOf(name) === -1) return false
      return true
    })
    const pageList = mockList.filter((item, index) => index < limit * page && index >= limit * (page - 1))
    return {
      code: 20000,
      count: mockList.length,
      list: pageList
    }
  },
    /**
   * 增加项目
   * // id, name, methodNum, imgNum, createdTime, updateTime, methodScore, methodImg
   * @param name, methodNum, imgNum, createdTime, updateTime, methodScore, methodImg
   * @return {{code: number, data: {message: string}}}
   */
    createPro: config => {
        const { name, methodNum, imgNum, methodImg} = JSON.parse(config.body)
        console.log(JSON.parse(config.body))
        methodName =[]
        for (let i = 0; i < methodNum; i++){
          methodName.push('method' + (i+1))
        }
        List.unshift({
          id: Mock.Random.guid(),
          name: name,
          methodNum: methodNum,
          imgNum: imgNum,
          methodImg: methodImg,
          createdTime: Mock.mock('@now'),
          updateTime: Mock.mock('@now'),
          methodScore: [],
          methodImg: methodImg,
          methodName: methodName
        })
        return {
          code: 20000,
          data: {
            message: '添加成功'
          }
        }
      },
  /**
   * 删除用户
   * @param id
   * @return {*}
   */
  deletePro: config => {
    // const { id } = param2Obj(config.url)
    const { id } = JSON.parse(config.body)
    if (!id) {
      return {
        code: -999,
        message: '参数不正确'
      }
    } else {
      List = List.filter(u => u.id !== id)
      return {
        code: 20000,
        message: '删除成功'
      }
    }
  },
  /**
   * 批量删除
   * @param config
   * @return {{code: number, data: {message: string}}}
   */
  batchremove: config => {
    let { ids } = param2Obj(config.url)
    ids = ids.split(',')
    List = List.filter(u => !ids.includes(u.id))
    return {
      code: 20000,
      data: {
        message: '批量删除成功'
      }
    }
  },
  /**
   * 修改用户
   * // id, name, methodNum, imgNum, createdTime, updateTime, methodScore, methodImg
   * @param id, name, methodScore
   * @return {{code: number, data: {message: string}}}
   */
  updatePro: config => {
    const { id, name, methodScore } = JSON.parse(config.body)
    // const sex_num = parseInt(sex)
    List.some(u => {
      if (u.id === id) {
        u.name = name
        u.methodScore = methodScore
        return true
      }
    })
    return {
      code: 20000,
      data: {
        message: '编辑成功'
      }
    }
  }
}

