<template>
  <div>
  
    <el-menu
        default-active="2"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#545c64"
        text-color="#fff"
        :collapse="iscollapse"
        active-text-color="#ffd04b">
        <span class="menutitle">{{iscollapse? 'MRI':'MRI图像打分平台'}}</span>
        <el-menu-item @click = "clickMenu(item)" v-for="item in menuData" :key="item.name" :index="item.name">
            <i :class="`el-icon-${item.icon}`"></i>
            <span slot="title">{{item.label}}</span>
        </el-menu-item>
        </el-menu>
  </div>
</template>

<script>
export default {
    data() {
        return {
            menuData: [
        {
          path: "/",
          name: "home",
          label: "首页",
          icon: "s-home",
          url: "Home/Home",
        },
        {
          path: "/score",
          name: "score",
          label: "打分界面",
          icon: "video-play",
          url: "Score/Score",
        },]
            
        }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      clickMenu(item){
        if(this.$route.path !== item.path && !(this.$route.path ==='/home' && item.path ==='/')){
            this.$router.push(item.path)
        }
      },
    },
    computed: {
        iscollapse(){
            return this.$store.state.tab.iscollapse
        }
    },
}

</script>

<style>
.el-menu{
    height: 100vh;
    transition: all 5ms;
    border-right: none;
}
.menutitle{
    color: #fff;
    text-align: center;
    font-weight: 400;
    line-height: 60px;
    padding: 0 15px;
}
body{
    margin: 0;
}

</style>
