<template>
    <div>
        <!-- <div class="manage" :class="{'show': isshow}"> -->
        <div class="manage" v-if="!isshow">
            <!-- <commonForm></commonForm> -->
            <el-button @click="handleAdd" type="primary">
            + 新增打分
            </el-button>
            <div class="ctabel">
                <el-table
                    stripe
                    height="400px"
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                        prop="name"
                        label="项目名称"
                        >
                    </el-table-column>
                    <el-table-column
                        prop="methodNum"
                        label="方法数">
                    </el-table-column>
                    <el-table-column
                        prop="imgNum"
                        label="图片数">
                    </el-table-column>
                    <el-table-column
                        prop="createdTime"
                        label="创建时间"
                        >
                    </el-table-column>
                    <el-table-column
                        prop="updateTime"
                        label="更新时间">
                    </el-table-column>
                    <el-table-column
                        label="操作">
                        <template slot-scope="scope">
                            <div class = "operate">
                                <el-button size="mini" @click="handleScore(scope.row)">点击评分</el-button>
                                <downExcel :number1="scope.row.methodName" :number2="scope.row.methodScore" :nam="scope.row.name"></downExcel>
                                <el-button class = "deletebtn" type ="danger" size="mini" @click="handleDelete(scope.row)">删除项目</el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pager">
                    <el-pagination
                        layout="prev, pager, next"
                        :total="total"
                        @current-change="handlePage"
                        >
                    </el-pagination>
                </div>
            </div>
        </div>
        <div v-else>
            <imgScore :number1="currRow.methodScore" :number2="currRow.methodImg" :isshow="isshow" @num1change = "num1change" @showchange = "showchange"></imgScore>
        </div>
        
    </div>

</template>

<script>
import { getPro, addPro, editPro, delPro} from "../api/index"
import downExcel from '../components/downExcel'
import imgScore from '../components/imgScore'
// import commonForm from '../components/commonForm'

export default {
    
    data() {
        return {
            tableData: [],
            total: 0, //当前的总条数
            isshow: false,
            currRow: {},
            pageData: {
                page: 1,
                limit: 10
            },
            rules: {
                name: [
                    { required: true, message: '请输入项目名称', trigger: 'blur' },
                ],
                methodNum: [
                    { required: true, message: '请输入方法数', trigger: 'blur' },
                ],

            },
            userForm:{
                name: '',
            }
        }
    },
    components: {
        downExcel,
        imgScore,
        // commonForm
    },
    methods: {
        getList(){
            getPro({params: {...this.userForm, ...this.pageData}}).then(({ data }) => {
            // console.log(data)
            this.tableData = data.list
            this.total = data.count || 0
            console.log('tableData', this.tableData)
            })
        },

        handlePage(val){
            // console.log(val)
            this.pageData.page = val
            this.getList()
        },
        handleDelete(row){
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                delPro({id: row.id}).then(() => {
                    this.$message({
                    type: 'success',
                    message: '删除成功!'
                    });
                    this.getList()
                })
                
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
            
        },
        handleScore(row){
            this.isshow = true
            // console.log(row)
            this.currRow = row
        },
        num1change(val){
            console.log('num1change1', val)
            console.log('this.currRow', this.currRow)
            this.currRow.methodScore = val
    
            editPro(this.currRow).then(() =>{
                // 重新获取列表的接口
                this.getList()
            })
            console.log('num1change2', val)
        },
        showchange(val){
            this.isshow = val
            console.log(this.isshow)
        },
        handleAdd(){
            this.$store.commit('dialogVisibleMenu')
        }
    },
    
    mounted() {
        
        this.getList()       
    },


}

</script>

<style>
.show{
    display: none;
}
.ctabel{
    position: relative;
    height: 430px;
}
.ctabel .pager{
    position: absolute;
    bottom: 0;
    right: 20px;
}

</style>

