<template>
    
  <div class="home">
        <downloadExcel
        class="export-excel-wrapper"
        :data="json_data"
        :fields="json_fields"
        type="xls"
        worksheet="My Worksheet"
        :name="flieName"
        >
        <el-button size="mini">下载评分</el-button>
        </downloadExcel>
    </div>


</template>

<script>
// import JsonExcel from 'vue-json-excel'
// // Vue.component('downloadExcel', JsonExcel)
export default {
    props: {
        number1: Array,
        number2: Array,
        nam: String,
    }, 
    data() {
        return {
            dnumber1: this.number1,           
            dnumber2: this.number2,
            flieName: this.nam,
            json_fields: {},           
            json_data: [],          
        }
    },
    methods:{
        updatefields(val1){
            for(let i = 0; i < val1.length; i++){                
                this.json_fields[val1[i]] = val1[i]
            }
        },
        updatedata(val1, val2){
            for(let i = 0; i < val2[0].length; i++){
                let  p = {}
                for(let j = 0; j < val2.length; j++){
                    p[val1[j]] = val2[j][i].score                 
                }
                this.json_data.push(p)
            }
        },
    },

    mounted() {      
        this.updatefields(this.dnumber1)
        this.updatedata(this.dnumber1, this.dnumber2)
        // console.log(this.json_data)          
    },
    
}

</script>
