<template>
    <div :class="{'show': !disshow}" class="imgall">
        <ul class="imgbar">
            <li class="imgbar-item" v-for="item in imgInd">
                <div class="imgslider">
                    <img :src=currImg[item] alt="">
                    <div class="block">
                        <el-slider @change = "scorechange"
                            v-model="currScore[item].score"
                            :step = "0.1"
                            :min = "0"
                            :max = "5"
                            show-stops>
                        </el-slider>
                    </div>                
                </div>
            </li>                          
        </ul>  
        <div class="btns">
            <el-button :disabled = prebtn size="mini" @click="preImg()">上一张</el-button>
            <el-button :disabled = nextbtn  size="mini" @click="nextImg()">下一张</el-button>
            <el-button size="mini" @click="tosubmit()">提交</el-button>
        </div>      
        
    </div>
</template>

<script>
export default {
    props: {
        number1: Array, //分数
        number2: Array, //图片
        isshow: Boolean,  //isshow
    }, 
    
    data() {
        return {  
            dnumber1: this.number1,   //分数    
            dnumber2: this.number2,   //图片
            disshow: this.isshow,
            currImg: [],
            currScore: [],
            imgInd:[],
            currSlice: 0,
            prebtn: false, 
            nextbtn: false,   
        }
    },
    methods:{
        getList(){
            console.log('this.dnumber1', this.dnumber1)
            console.log('this.dnumber2', this.dnumber2)
            this.currImg = []
            this.currScore = []
            for (let i = 0; i < this.dnumber2.length; i++) {
                this.currImg.push(this.dnumber2[i][this.currSlice])
                this.currScore.push(this.dnumber1[i][this.currSlice])  
            }
           
            console.log(this.currImg)
            console.log(this.currScore)
            console.log('currSlice', this.currSlice)

            let arrIndex = Array.from(Array(this.currImg.length).keys())
            for(let i=0; i<arrIndex.length + 10; i++){
                let rdm = Math.floor(Math.random()*arrIndex.length)
                arrIndex.push(arrIndex[rdm])
                arrIndex.splice(rdm,1)
            }
            this.imgInd = arrIndex
            console.log(this.imgInd)
        },
        preImg(){
            if(this.currSlice > 0){
                this.prebtn = false
                this.nextbtn = false
                this.currSlice--
                this.getList()
            
            }else{
                this.prebtn = true
                this.nextbtn = false
                this.$message({
                    message: '已经是第一张图片了',
                    type: 'warning'
                    });
            }         
        },
        nextImg(){
            if(this.currSlice < this.dnumber2[0].length - 1){
                this.prebtn = false
                this.nextbtn = false
                this.currSlice++
                this.getList()
            
            }else{
                this.nextbtn = true
                this.prebtn = false
                this.$message({
                    message: '已经是最后一张图片了',
                    type: 'warning'
                    })
            }  
        },
        scorechange(){
            // console.log(this.currScore)
            for (let i = 0; i < this.dnumber1.length; i++) {
                this.dnumber1[i][this.currSlice] = this.currScore[i]
                // console.log(this.dnumber1[i][this.currSlice])
            }        
        },
        tosubmit(){
            this.$emit('num1change',this.dnumber1)
            this.disshow = !this.disshow
            this.$emit('showchange',this.disshow)

            
        },
    },
    mounted() {       
        this.getList()       
    },
}

</script>
<style>
    .simg{
        width: 20%;
        padding-bottom: 100%; 
        height: 0;
        
    }
    .show{
        display: none;
    }
    .imgbar{
        list-style: none;
        display: flex;
        padding-left: 20px;  
        
    }
    .imgbar-item{
        flex: 1; 
        text-align: center; 
    }
    .imgslider{
        position: relative;
        width: 200px;
    }
    .el-slider{
        position: absolute;
        bottom: -60px;
        /* left: 10px; */
        width: 200px;
        text-align: center;
    }
    .el-slider__runway{
        margin: 16px 0;
    }
    .el-slider__stop{
        background-color: #cccccc;
    }
    .imgall{
        position: relative;
    }
    .btns{
        position: absolute;
        bottom: -80%;
        right: 40%;
        flex: 1; 
        text-align: center; 

    }

</style>
